.menu-btn {
  background-color: #3dcd58;
  color: white;
  /* padding: 16px; */
  font-size: 20px;
  font-weight: bolder;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border: none;
}
.dropdown-menu {
  position: relative;
  display: inline-block;
  background-color: #3dcd58;
  border: none;
  
}
.menu-content {
  display: none;
  position: absolute;
  background-color: #3dcd58;
  min-width: 160px;
  z-index: 1;
  /* padding: 16px; */
}
nav {
  background-color: #3dcd58;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    /* padding: 16px; */

}
.links,
.links-hidden {
  display: inline-block;
  color: rgb(255, 255, 255);
  padding: 12px 16px;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
}
.links-hidden {
  display: block;
}
.links {
  display: inline-block;
}
.links-hidden:hover,
.links:hover {
  /* background-color: #50ca60 ; */
  color: white;
}
.dropdown-menu:hover .menu-content {
  display: block;
}
.dropdown-menu:hover .menu-btn {
  
  background-color: #3e8e41;
}
